.container {
  max-width: var(--container-width);
  width: 100%;
  margin: 0px auto;
  padding-left: 20px;
  padding-right: 20px;

  // @include media-lg {
  //   // max-width: 970px;
  //   max-width: none;
  // }

  @media screen and (max-width: 1220px) {
    max-width: none;
  }

  @include media-md {
    max-width: none;
  }

  @include media-sm {
    max-width: none;
  }
}

.row {
  @include row();
  // 20px
  margin-left: -10px;
  margin-right: -10px;
}

